import { Injectable } from '@angular/core';
import { get } from 'lodash';

import { IDefinitionAdapter } from '@helix/platform/shared/api';
import { IContainerViewComponentDefinition, IViewDefinition, RX_LEGACY_ICONS } from '@helix/platform/view/api';

@Injectable()
export class RxShellMenuItemDesignAdapterService
  implements IDefinitionAdapter<IContainerViewComponentDefinition, IViewDefinition> {
  adaptDefinition(componentDefinition: IContainerViewComponentDefinition): void {
    if (componentDefinition.propertiesByName.menuItemIcon) {
      componentDefinition.propertiesByName.menuItemIcon = get(
        RX_LEGACY_ICONS,
        componentDefinition.propertiesByName.menuItemIcon,
        componentDefinition.propertiesByName.menuItemIcon
      );
    }
  }
}
